  margin-right: -12px;
}
@media (max-width: 576px) {
  .row {
    margin-left: -8px;
    margin-right: -8px;
  }
}

.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: inline-flex;
  display: -ms-flex;
  display: -ms-inline-flexbox;
  display: -ms-flexbox;
  display: flex;
  flex-direction: row;
}

.valign-center {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  -ms-align-items: center;
  align-items: center;
}

.justify-center {
  -webkit-justify-content: center;
  -ms-justify-content: center;
  justify-content: center;
}

.space-between {
  justify-content: space-between;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

[class*=col-] {
  width: 100%;
  padding-left: 12px;
  padding-right: 12px;
}
@media (max-width: 576px) {
  [class*=col-] {
    padding-left: 8px;
    padding-right: 8px;
  }
}

.clear {
  padding-left: 0px;
  padding-right: 0px;
}

