.main-contacts-tel-arrow {
  display: inline-block;
  position: absolute;
  margin-left: 6px;
  bottom: 2px;
  max-width: 12px;
  cursor: pointer;
}
.main-contacts-tel-arrow img {
  min-width: 12px;
}

.main-contacts-drop {
  display: none;
  position: absolute;
  z-index: 15;
  width: 222px;
  left: 0;
  top: 55px;
  background: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.08);
  border-radius: 0px 0px 8px 8px;
  padding: 15px 16px;
  border-top: 4px solid #bc0000;
}
.main-contacts-drop-group {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #D3D3D3;
}
.main-contacts-drop-title {
  font-size: 0.75rem;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 6px;
}
.main-contacts-drop-num {
  display: block;
  font-weight: 700;
  font-size: 0.875rem;
  margin-bottom: 8px;
  padding-left: 24px;
  position: relative;
}
.main-contacts-drop-num:hover {
  color: #2A4455;
}
.main-contacts-drop-num-img {
  position: absolute;
  transform: translate(0, -50%);
  top: 50%;
  left: 0;
  max-width: 16px;
}
.main-contacts-drop-num-img img {
  display: block;
}
.main-contacts-drop-mail {
  color: #1a66ae !important;
  font-weight: 400;
}
.main-contacts-drop-socials {
  border-bottom: 0;
  padding-bottom: 0;
  margin-bottom: 0;
  font-size: 0;
  margin-left: -5px;
  margin-right: -5px;
  margin-bottom: -10px;
  white-space: normal;
  text-align: center;
}
.main-contacts-drop-social {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #D3D3D3;
  margin: 0px 5px 10px 5px;
  transition: border 0.3s, background-color 0.3s;
  padding: 8px;
}
.main-contacts-drop-social svg {
  fill: #D3D3D3;
}
.main-contacts-drop-social:hover {
  border-color: transparent;
}
.main-contacts-drop-social.--blue:hover {
  background-color: #8CB2D6;
}
.main-contacts-drop-social.--blue:hover svg {
  fill: #1A66AE;
}
.main-contacts-drop-social.--red:hover {
  background-color: #DF908A;
}
.main-contacts-drop-social.--red:hover svg {
  fill: #BC0000;
}
.main-contacts-drop-social.--yellow:hover {
  background-color: #F9DE8E;
}
.main-contacts-drop-social.--yellow:hover svg {
  fill: #FFC100;
}
@media (max-width: 1024.99px) {
  .main-contacts-drop {
    left: auto;
    right: 0;
    width: 416px;
    top: 100%;
    margin-top: 15px;
  }
  .main-contacts-drop-title {
    font-size: 14px;
    font-size: 0.875rem;
  }
  .main-contacts-drop-num {
    font-size: 16px;
    font-size: 1rem;
  }
  .main-contacts-drop-socials {
    margin-left: -4px;
